// drawing-service.ts

import { CONFIG } from './canvas-config'
import type { PathCommand } from './canvas-config'
import type { Rect } from './rect' // Rect  

type CanvasLineCap = 'butt' | 'round' | 'square'
type CanvasLineJoin = 'round' | 'bevel' | 'miter'

export class DrawingService {
  private ctx: CanvasRenderingContext2D
  private historyCanvas: HTMLCanvasElement
  private historyCtx: CanvasRenderingContext2D

  constructor(private canvas: HTMLCanvasElement) {
    this.ctx = canvas.getContext('2d')!

    //Initialize history canvas
    this.historyCanvas = document.createElement('canvas')
    this.historyCanvas.width = canvas.width
    this.historyCanvas.height = canvas.height
    this.historyCtx = this.historyCanvas.getContext('2d')!
  }
  drawRect(rect: Rect) {
    this.ctx.save()
    this.ctx.strokeStyle = 'yellow'
    this.ctx.lineWidth = 1
    this.ctx.strokeRect(rect.x + 2, rect.y + 2, rect.w - 4, rect.h - 4)
    this.ctx.restore()
  }

  // Core drawing methods
  clearCanvas() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)
  }

  drawStartPoint(x: number, y: number, number: number) {
    this.ctx.fillStyle = CONFIG.startPoint.color
    this.ctx.beginPath()
    this.ctx.arc(x, y, CONFIG.startPoint.radius, 0, Math.PI * 2)
    this.ctx.fill()

    const fontSize = Math.max(12, Math.floor(CONFIG.startPoint.radius * 1.2))
    this.ctx.fillStyle = CONFIG.startPoint.textColor
    this.ctx.font = `${fontSize}px Arial`
    this.ctx.textAlign = 'center'
    this.ctx.textBaseline = 'middle'
    this.ctx.fillText(number.toString(), x, y)
  }

  drawEndPoint(x: number, y: number) {
    this.ctx.save()
    this.ctx.fillStyle = CONFIG.startPoint.color
    this.ctx.beginPath()
    this.ctx.arc(x, y, CONFIG.startPoint.radius * 0.5, 0, Math.PI * 2)
    this.ctx.fill()
    this.ctx.restore()
  }

  drawStrokePath(coords: any) {
    // console.log('Drawing stroke path:', coords)
    this.ctx.save()

    // Draw background
    this.ctx.strokeStyle = CONFIG.paths.background.color
    this.ctx.lineWidth =
      CONFIG.paths.guide.width + CONFIG.paths.background.widthOffset
    this.ctx.lineCap = 'round'
    this.ctx.lineJoin = 'round'
    this.drawPathDetails(coords)
    this.ctx.stroke()

    // Draw guideline
    this.ctx.strokeStyle = CONFIG.paths.guide.color
    this.ctx.lineWidth = CONFIG.paths.guide.width
    this.ctx.setLineDash(CONFIG.paths.guide.dash)
    this.drawPathDetails(coords)
    this.ctx.stroke()

    this.ctx.restore()
  }

  private drawPathDetails(coords: any) {
    // console.log('drawPathDetails called with:', coords) // 
    for (let i = 1; i <= Object.keys(coords).length; i++) {
      const stroke = coords[`stroke${i}`]
      if (!stroke) continue

      this.ctx.save()
      this.ctx.beginPath()

      if (stroke.type === 'circle') {
        // console.log('Drawing circle:', stroke)
        this.ctx.ellipse(
          stroke.center.x,
          stroke.center.y,
          stroke.radiusX,
          stroke.radiusY,
          0,
          0,
          Math.PI * 2
        )
        continue
      } else {
        // console.log('Drawing path:', stroke) // 
        this.ctx.moveTo(stroke.start.x, stroke.start.y)

        switch (stroke.type) {
          case 'quadratic':
            this.ctx.quadraticCurveTo(
              stroke.control.x,
              stroke.control.y,
              stroke.end.x,
              stroke.end.y
            )
            break

          case 'path':
            this.drawCommandPath(stroke.commands)
            break

          default:
            if (stroke.middle) {
              this.ctx.lineTo(stroke.middle.x, stroke.middle.y)
            }
            this.ctx.lineTo(stroke.end.x, stroke.end.y)
        }
      }
      this.ctx.stroke()
    }
  }

  private drawCommandPath(commands: PathCommand[]) {
    commands.forEach((command) => {
      switch (command.type) {
        case 'M':
          this.ctx.moveTo(command.x ?? 0, command.y ?? 0)
          break
        case 'L':
          this.ctx.lineTo(command.x ?? 0, command.y ?? 0)
          break
        case 'Q':
          if (command.control && command.end) {
            this.ctx.quadraticCurveTo(
              command.control.x,
              command.control.y,
              command.end.x,
              command.end.y
            )
          }
          break
      }
    })
  }

  // User stroke methods
  drawUserStroke(points: Array<[number, number]>) {
    if (points.length < 2) return

    this.ctx.beginPath()
    this.ctx.setLineDash([]) //   
    this.ctx.moveTo(points[0][0], points[0][1])

    for (let i = 1; i < points.length; i++) {
      this.ctx.lineTo(points[i][0], points[i][1])
    }
    this.ctx.stroke()
  }

  addStrokeToHistory(stroke: Array<[number, number]>) {
    this.historyCtx.strokeStyle = CONFIG.animation.pathColor
    this.historyCtx.lineWidth = CONFIG.paths.drawing.width
    this.historyCtx.lineCap = CONFIG.paths.drawing.cap as CanvasLineCap
    this.historyCtx.lineJoin = CONFIG.paths.drawing.join as CanvasLineJoin

    this.historyCtx.beginPath()
    const firstPoint = stroke[0]
    this.historyCtx.moveTo(firstPoint[0], firstPoint[1])

    for (let i = 1; i < stroke.length; i++) {
      const point = stroke[i]
      this.historyCtx.lineTo(point[0], point[1])
    }
    this.historyCtx.stroke()
  }

  // drawHistory() {
  //   this.ctx.drawImage(this.historyCanvas, 0, 0)
  //   console.log('Drawing history:', {
  //     historyCanvas: this.historyCanvas,
  //     mainCanvas: this.canvas,
  //     historyContext: this.historyCtx,
  //     mainContext: this.ctx
  //   })
  // }

  drawHistory() {
    //      
    this.ctx.drawImage(this.historyCanvas, 0, 0)
  }

  // Canvas size management
  updateCanvasSize(width: number, height: number) {
    this.canvas.width = width
    this.canvas.height = height
    this.historyCanvas.width = width
    this.historyCanvas.height = height
  }

  drawLineWithArrow(
    startX: number,
    startY: number,
    endX: number,
    endY: number,
    angle: number
  ) {
    this.ctx.beginPath()
    this.ctx.moveTo(startX, startY)
    this.ctx.lineTo(endX, endY)
    this.ctx.stroke()
    this.drawArrow(endX, endY, angle)
  }

  // Animation methods
  drawArrow(x: number, y: number, angle: number) {
    const size = CONFIG.animation.arrowSize * 2

    this.ctx.save()
    this.ctx.translate(x, y)
    this.ctx.rotate(angle)
    this.ctx.fillStyle = CONFIG.animation.arrowColor
    this.ctx.beginPath()
    this.ctx.moveTo(0, 0)
    this.ctx.lineTo(-size * 2, -size)
    this.ctx.lineTo(-size * 1.5, 0)
    this.ctx.lineTo(-size * 2, size)
    this.ctx.closePath()
    this.ctx.fill()
    this.ctx.restore()
  }

  getAngle(startX: number, startY: number, endX: number, endY: number): number {
    return Math.atan2(endY - startY, endX - startX)
  }

  setAnimationStyle(color: string) {
    this.ctx.save()
    this.ctx.strokeStyle = color
    this.ctx.lineWidth = CONFIG.paths.drawing.width
    this.ctx.lineCap = 'round'
    this.ctx.lineJoin = 'round'
    this.ctx.setLineDash([])
  }

  drawCompleteLine(startX: number, startY: number, endX: number, endY: number) {
    this.ctx.beginPath()
    this.ctx.moveTo(startX, startY)
    this.ctx.lineTo(endX, endY)
    this.ctx.stroke()
  }

  drawCircle(
    centerX: number,
    centerY: number,
    radiusX: number,
    radiusY: number,
    startAngle: number,
    endAngle: number
  ) {
    this.ctx.beginPath()
    this.ctx.ellipse(
      centerX,
      centerY,
      radiusX,
      radiusY,
      0,
      startAngle,
      endAngle,
      true
    )
    this.ctx.stroke()
  }

  drawQuadraticCurve(
    startX: number,
    startY: number,
    controlX: number,
    controlY: number,
    endX: number,
    endY: number
  ) {
    this.ctx.beginPath()
    this.ctx.moveTo(startX, startY)
    this.ctx.quadraticCurveTo(controlX, controlY, endX, endY)
    this.ctx.stroke()
  }

  restoreContext() {
    this.ctx.restore()
  }

  drawStrokes(coords: any, maxStrokes?: number) {
    const strokeCount = maxStrokes ?? Object.keys(coords).length

    //    
    this.ctx.save()
    this.ctx.strokeStyle = CONFIG.paths.drawing.color
    this.ctx.lineWidth = CONFIG.paths.drawing.width
    this.ctx.lineCap = CONFIG.paths.drawing.cap as CanvasLineCap
    this.ctx.lineJoin = CONFIG.paths.drawing.join as CanvasLineJoin

    for (let i = 1; i <= strokeCount; i++) {
      const stroke = coords[`stroke${i}`]
      if (!stroke) continue

      switch (stroke.type) {
        case 'circle':
          this.drawCircle(
            stroke.center.x,
            stroke.center.y,
            stroke.radiusX,
            stroke.radiusY,
            0,
            Math.PI * 2
          )
          break

        case 'quadratic':
          this.drawQuadraticCurve(
            stroke.start.x,
            stroke.start.y,
            stroke.control.x,
            stroke.control.y,
            stroke.end.x,
            stroke.end.y
          )
          break

        case 'path':
          if (stroke.commands) {
            this.drawCompleteLine(
              stroke.commands[0].x,
              stroke.commands[0].y,
              stroke.commands[1].x,
              stroke.commands[1].y
            )

            if (stroke.commands[2]) {
              this.drawQuadraticCurve(
                stroke.commands[1].x,
                stroke.commands[1].y,
                stroke.commands[2].control.x,
                stroke.commands[2].control.y,
                stroke.commands[2].end.x,
                stroke.commands[2].end.y
              )
            }
          }
          break

        default: // 'line' type
          if (stroke.middle) {
            this.drawCompleteLine(
              stroke.start.x,
              stroke.start.y,
              stroke.middle.x,
              stroke.middle.y
            )
            this.drawCompleteLine(
              stroke.middle.x,
              stroke.middle.y,
              stroke.end.x,
              stroke.end.y
            )
          } else {
            this.drawCompleteLine(
              stroke.start.x,
              stroke.start.y,
              stroke.end.x,
              stroke.end.y
            )
          }
      }
    }
    this.ctx.restore()
  }
  restore() {
    this.ctx.restore()
  }
}
