<script lang="ts">
  import { type KoWritingResponse } from './model/canvas-handler'

  //      
  interface StrokeDetail {
    startOk: boolean
    middleOk: boolean
    endOk: boolean
    firstPathOk: boolean
    secondPathOk: boolean
  }

  interface WritingResponse {
    success: boolean
    stroke: {
      current: number
      total: number
      type?: 'line' | 'circle' | 'quadratic' | 'path'
    }
    detail?: StrokeDetail
  }

  export let result: WritingResponse | undefined = undefined

  function isCircleCharacter(response: WritingResponse) {
    // strokesArray        
    //        
    return false //  
  }

  //     
  function getDetailMessage(detail: any) {
    if (!detail) return ''
    const messages = []

    if (!detail.startOk) messages.push('')
    if (!detail.firstPathOk) messages.push(' ')
    if (!detail.middleOk) messages.push('')
    if (!detail.secondPathOk) messages.push(' ')
    if (!detail.endOk) messages.push('')

    if (messages.length === 0) return ''
    return `${messages.join(', ')}  !`
  }
</script>

<div class="fb">
  {#if result && result.stroke.current > 0}
    {#if result.success}
      {#if result.stroke.current === result.stroke.total}
        <span class="success">!</span>
      {:else}
        <span>{result.stroke.current + 1}  .</span>
      {/if}
    {:else}
      <div class="error-content">
        <img
          src="https://kr.object.ncloudstorage.com/tesis/media/x0.png"
          alt=""
        />
        <div class="error-messages">
          <span class="error">
            {#if result.stroke.type === 'circle'}
               !
            {:else}
              {result.stroke.current}  !
            {/if}
          </span>
          {#if result.detail}
            {@const detailMessage = getDetailMessage(result.detail)}
            {#if detailMessage && result.stroke.type != 'circle'}
              <span class="error detail">{detailMessage}</span>
            {/if}
          {/if}
        </div>
      </div>
    {/if}
  {/if}
</div>

