/**
 * (x, y, width, height)  
 */
export class Rect {
  constructor(
    private _x: number,
    readonly y: number,
    readonly w: number,
    readonly h: number
  ) {}
  get x() {
    return this._x
  }
  setX(x: number) {
    this._x = x
  }
}

export class NullRect extends Rect {
  constructor() {
    super(0, 0, 0, 0)
  }
}
