export class RegionBlock {
  offsetX: number // 0 | 0.6
  offsetY: number // 0 | 0
  width: number // 0.6  | 0.4
  height: number // 1  | 1
  coords: unknown //
  constructor(
    x: number,
    y: number,
    width: number,
    height: number,
    coords?: unknown
  ) {
    this.offsetX = x
    this.offsetY = y
    this.width = width
    this.height = height
    this.coords = coords
  }
}
