import {} from 'svelte/store';
export * from './storage/index.js';
const dummy = () => { };
const updateStore = (store, callback) => {
    store.update((state) => {
        callback(state);
        return state;
    });
};
export class SvelteStore {
    constructor() { }
    subscribe(callback) {
        return this.store.subscribe(callback);
    }
    update(callback) {
        updateStore(this.store, callback || dummy);
    }
    set() {
        this.update();
    }
}
