<script lang="ts">
  import { onMount } from 'svelte'
  import { CanvasHandler, type KoWritingResponse } from './model/canvas-handler'
  import SuccessPanel from './SuccessPanel.svelte'
  import type { Jamo } from './model/jamo'

  const {
    ready,
    end,
    feedback,
    chars
  }: {
    ready?: (handler: CanvasHandler) => void
    end?: (res: KoWritingResponse) => void
    feedback?: (res: KoWritingResponse) => void
    chars: Jamo[]
  } = $props()

  let canvasEl: HTMLCanvasElement
  let handler = $state<CanvasHandler>()
  let { showFeedback: feedbackVisible, image } = $state({
    showFeedback: false,
    image: ''
  })
  let canvasSize = 500

  const hideFeedback = () => {
    feedbackVisible = false
    image = ''
    if (feedback) {
      feedback(undefined)
    }
  }
  const handleRepsonse = (res: KoWritingResponse) => {
    if (end) {
      end(res)
    }
    feedbackVisible = res.success && res.stroke.current === res.stroke.total
    image = 'https://kr.object.ncloudstorage.com/tesis/media/o0.png'
    if (feedback) {
      feedback(res)
    }
  }
  onMount(() => {
    handler = new CanvasHandler(canvasEl!, { char: chars[0] })
    handler.bindEventListener(handleRepsonse)
    if (ready) {
      ready(handler)
    }
  })
</script>

<div class="canvas-container">
  <canvas width="500" height="500" bind:this={canvasEl}></canvas>
  {#if handler && feedbackVisible}
    <SuccessPanel
      size={canvasSize}
      {image}
      ratio={0.7}
      on:click={hideFeedback}
    />
  {/if}
</div>

<style lang="scss">
  .canvas-container {
    position: relative;
    width: 100%;
    overflow-x: auto;
    canvas {
      display: block;
      margin: 0 auto;
      background-color: #2f2f2f;
    }
  }
</style>
