import { CHAR_PATHS, CONFIG, type PathCommand } from '../model/canvas-config'
import type { CanvasHandler } from '../model/canvas-handler.ts'
import { DrawingService } from '../model/drawing-service'
import type { Jamo } from '../model/jamo'
import { TTSService } from '../lib/tts'
import { combineJamo } from '../lib/hangul-utils'

const tts = new TTSService()

export class CharacterAnimation {
  private startTime: number
  private baseTime = 100 // 2000 1000 
  private drawingService: DrawingService
  private strokeSpeedFactors = {
    line: {
      single: 1, //    
      double: 1.5 //  (2  )
    },
    circle: 1.2, // 
    quadratic: 1.3, // 
    path: {
      horizontal: 0.8, // 
      curve: 1.2 //  
    }
  }
  private currentChar: string = ''
  private lastAnimatedIndex: number = -1

  private completedStrokes: Array<Array<[number, number]>> = []

  // private animationStrokes: Map<number, Array<Array<[number, number]>>> =
  //   new Map()

  private isAnimating: boolean = false
  private animationFrameId: number | null = null

  constructor(readonly handler: CanvasHandler, readonly pathes: any) {
    this.startTime = Date.now()
    this.drawingService = new DrawingService(handler.canvas)

    if (!handler.jamos || !Array.isArray(handler.jamos)) {
      throw new Error('Handler jamos is not properly initialized')
    }

    // console.log('Current mode:', handler.mode)
    // console.log('Jamos:', handler.jamos)
    // console.log('Pathes:', pathes)
  }

  private calculateStrokeLength(stroke: any): number {
    switch (stroke.type) {
      case 'line':
        if (stroke.middle) {
          return (
            this.getLineLength(stroke.start, stroke.middle) +
            this.getLineLength(stroke.middle, stroke.end)
          )
        }
        return this.getLineLength(stroke.start, stroke.end)

      case 'circle':
        return Math.PI * 2 * Math.max(stroke.radiusX, stroke.radiusY)

      case 'quadratic':
        return this.getQuadraticCurveLength(
          stroke.start,
          stroke.control,
          stroke.end
        )

      case 'path':
        return this.getPathLength(stroke.commands)

      default:
        return 100 //  
    }
  }

  private getLineLength(
    start: { x: number; y: number },
    end: { x: number; y: number }
  ): number {
    return Math.sqrt(
      Math.pow(end.x - start.x, 2) + Math.pow(end.y - start.y, 2)
    )
  }

  private getQuadraticCurveLength(
    start: { x: number; y: number },
    control: { x: number; y: number },
    end: { x: number; y: number }
  ): number {
    const segments = 10
    let length = 0
    let prevPoint = start

    for (let i = 1; i <= segments; i++) {
      const t = i / segments
      const point = {
        x:
          Math.pow(1 - t, 2) * start.x +
          2 * (1 - t) * t * control.x +
          Math.pow(t, 2) * end.x,
        y:
          Math.pow(1 - t, 2) * start.y +
          2 * (1 - t) * t * control.y +
          Math.pow(t, 2) * end.y
      }
      length += this.getLineLength(prevPoint, point)
      prevPoint = point
    }

    return length
  }

  private getPathLength(commands: PathCommand[]): number {
    let length = 0
    let prevPoint = { x: 0, y: 0 }

    commands.forEach((cmd) => {
      switch (cmd.type) {
        case 'M':
          prevPoint = { x: cmd.x ?? 0, y: cmd.y ?? 0 }
          break
        case 'L':
          if (cmd.x !== undefined && cmd.y !== undefined) {
            length += this.getLineLength(prevPoint, { x: cmd.x, y: cmd.y })
            prevPoint = { x: cmd.x, y: cmd.y }
          }
          break
        case 'Q':
          if (cmd.control && cmd.end) {
            length += this.getQuadraticCurveLength(
              prevPoint,
              cmd.control,
              cmd.end
            )
            prevPoint = cmd.end
          }
          break
      }
    })

    return length
  }

  private getStrokeDuration(stroke: any): number {
    const length = this.calculateStrokeLength(stroke)
    let speedFactor = 1

    switch (stroke.type) {
      case 'line':
        speedFactor = stroke.middle
          ? this.strokeSpeedFactors.line.double
          : this.strokeSpeedFactors.line.single
        break
      case 'circle':
        speedFactor = this.strokeSpeedFactors.circle
        break
      case 'quadratic':
        speedFactor = this.strokeSpeedFactors.quadratic
        break
      case 'path':
        speedFactor = 1
        break
    }

    return this.baseTime * speedFactor * (length / 100)
  }

  animate(progress: number, currentCharacter: Jamo, coords: any) {
    const { handler } = this

    if (currentCharacter === '_') return

    if (!handler.jamos || !coords) {
      console.error('Required data is missing')
      return
    }

    let currentJamoIndex = handler.jamos.indexOf(currentCharacter)
    if (currentJamoIndex === -1) {
      console.error('Current character not found in handler.jamos.')
      return
    }

    if (currentJamoIndex === 0 && handler.currentJamoIndex === 2) {
      currentJamoIndex = 2
    }

    const prevStrokes =
      currentJamoIndex > 0 ? handler.strokesArray[currentJamoIndex - 1] : 0
    const currentTotalStrokes =
      handler.strokesArray[currentJamoIndex] - prevStrokes
    const strokeDuration = 1 / currentTotalStrokes
    const currentStrokeIndex = Math.floor(progress / strokeDuration)
    const strokeProgress =
      (progress - currentStrokeIndex * strokeDuration) / strokeDuration

    //     

    const actualStrokeIndex = currentStrokeIndex + 1
    if (coords[`stroke${actualStrokeIndex}`]) {
      const currentStroke = coords[`stroke${actualStrokeIndex}`]
      let points: Array<[number, number]> = []

      switch (currentStroke.type) {
        case 'line':
          points = this.animateLine(currentStroke, strokeProgress)
          break
        case 'circle':
          points = this.animateCircle(currentStroke, strokeProgress)
          break
        case 'quadratic':
          points = this.animateQuadratic(currentStroke, strokeProgress)
          break
        case 'path':
          points = this.animatePath(currentStroke, strokeProgress)
          break
      }

      this.drawingService.drawUserStroke(points)

      if (strokeProgress === 1) {
        this.completedStrokes.push(points)
      }
    }

    this.drawingService.restore() //  
    this.lastAnimatedIndex = currentJamoIndex
  }

  private animateLine(
    stroke: any,
    strokeProgress: number
  ): Array<[number, number]> {
    const points: Array<[number, number]> = []

    if (stroke.middle) {
      if (strokeProgress <= 0.5) {
        //    
        const progress = strokeProgress * 2
        points.push([stroke.start.x, stroke.start.y])

        for (let t = 0; t <= progress; t += 0.02) {
          const x = stroke.start.x + (stroke.middle.x - stroke.start.x) * t
          const y = stroke.start.y + (stroke.middle.y - stroke.start.y) * t
          points.push([x, y])
        }
        //       
        if (progress >= 0.9) {
          points.push([stroke.middle.x, stroke.middle.y])
        }
      } else {
        //    
        const progress = (strokeProgress - 0.5) * 2

        //      
        points.push([stroke.start.x, stroke.start.y])
        points.push([stroke.middle.x, stroke.middle.y])

        for (let t = 0; t <= progress; t += 0.02) {
          const x = stroke.middle.x + (stroke.end.x - stroke.middle.x) * t
          const y = stroke.middle.y + (stroke.end.y - stroke.middle.y) * t
          points.push([x, y])
        }
        //       
        if (progress >= 0.9) {
          points.push([stroke.end.x, stroke.end.y])
        }
      }
    } else {
      //  
      points.push([stroke.start.x, stroke.start.y])

      for (let t = 0; t <= strokeProgress; t += 0.02) {
        const x = stroke.start.x + (stroke.end.x - stroke.start.x) * t
        const y = stroke.start.y + (stroke.end.y - stroke.start.y) * t
        points.push([x, y])
      }
      //     
      if (strokeProgress >= 0.9) {
        points.push([stroke.end.x, stroke.end.y])
      }
    }

    return points
  }

  private animateCircle(
    stroke: any,
    strokeProgress: number
  ): Array<[number, number]> {
    const points: Array<[number, number]> = []
    const startAngle = -Math.PI / 2
    const endAngle = startAngle - Math.PI * 2 * strokeProgress

    //     
    for (let angle = startAngle; angle >= endAngle; angle -= 0.05) {
      const x = stroke.center.x + Math.cos(angle) * stroke.radiusX
      const y = stroke.center.y + Math.sin(angle) * stroke.radiusY
      points.push([x, y])
    }

    //      
    if (strokeProgress >= 0.9) {
      const finalX = stroke.center.x + Math.cos(endAngle) * stroke.radiusX
      const finalY = stroke.center.y + Math.sin(endAngle) * stroke.radiusY
      points.push([finalX, finalY])
    }

    return points
  }

  private animateQuadratic(
    stroke: any,
    strokeProgress: number
  ): Array<[number, number]> {
    const points: Array<[number, number]> = []
    points.push([stroke.start.x, stroke.start.y])

    //     
    for (let t = 0; t <= strokeProgress; t += 0.02) {
      const x =
        Math.pow(1 - t, 2) * stroke.start.x +
        2 * (1 - t) * t * stroke.control.x +
        Math.pow(t, 2) * stroke.end.x

      const y =
        Math.pow(1 - t, 2) * stroke.start.y +
        2 * (1 - t) * t * stroke.control.y +
        Math.pow(t, 2) * stroke.end.y

      points.push([x, y])
    }

    //     
    if (strokeProgress >= 0.9) {
      points.push([stroke.end.x, stroke.end.y])
    }

    return points
  }

  private animatePath(
    stroke: any,
    strokeProgress: number
  ): Array<[number, number]> {
    const points: Array<[number, number]> = []
    const commands = stroke.commands

    if (strokeProgress <= 0.5) {
      //    
      const progress = strokeProgress * 2
      points.push([commands[0].x, commands[0].y])

      const x = commands[0].x + (commands[1].x - commands[0].x) * progress
      points.push([x, commands[0].y])

      //     
      for (let t = 0; t <= progress; t += 0.02) {
        const x = commands[0].x + (commands[1].x - commands[0].x) * t
        points.push([x, commands[0].y])
      }
      //       
      if (progress >= 0.8) {
        points.push([commands[1].x, commands[1].y])
      }
    } else {
      //   
      const progress = (strokeProgress - 0.5) * 2

      //      
      points.push([commands[0].x, commands[0].y])
      points.push([commands[1].x, commands[1].y])

      //   
      for (let t = 0; t <= progress; t += 0.02) {
        const x =
          Math.pow(1 - t, 2) * commands[1].x +
          2 * (1 - t) * t * commands[2].control.x +
          Math.pow(t, 2) * commands[2].end.x

        const y =
          Math.pow(1 - t, 2) * commands[1].y +
          2 * (1 - t) * t * commands[2].control.y +
          Math.pow(t, 2) * commands[2].end.y

        points.push([x, y])
      }
      //     
      if (progress >= 0.9) {
        points.push([commands[2].end.x, commands[2].end.y])
      }
    }

    return points
  }

  // private getPointOnLine(
  //   startX: number,
  //   startY: number,
  //   endX: number,
  //   endY: number,
  //   progress: number
  // ): [number, number] {
  //   return [
  //     startX + (endX - startX) * progress,
  //     startY + (endY - startY) * progress
  //   ]
  // }

  private handleTTS(index: number) {
    if (this.handler.mode === 'jamo') {
      const currentJamo = this.handler.jamos[index]
      if (this.currentChar !== currentJamo) {
        tts.speak(currentJamo)
        this.currentChar = currentJamo
      }
    } else {
      const cho = Math.floor(index / 3) * 3

      const jamos = this.handler.jamos.slice(cho, cho + 3)
      if (jamos[0] && jamos[1]) {
        const char = combineJamo(jamos)
        if (char && this.currentChar !== char) {
          tts.speak(char)
          this.currentChar = char
        }
      }
    }
  }

  start() {
    const { handler } = this
    const { jamos } = handler

    //      
    if (this.isAnimating) {
      if (this.animationFrameId) {
        cancelAnimationFrame(this.animationFrameId)
      }
      this.drawingService.clearCanvas()
      handler.drawPathes()
      handler.drawStartPoints()
    }

    this.startTime = Date.now()
    this.lastAnimatedIndex = -1
    this.completedStrokes = []
    // this.animationStrokes.clear()

    //       
    const jamoTimings = jamos.map((jamo, index) => {
      //  strokes null  
      const strokes = this.pathes[index]
      let totalDuration = 0
      let strokeCount = 0

      // null undefined   
      if (strokes && typeof strokes === 'object') {
        //     CHAR_PATHS 
        const charPath = CHAR_PATHS[jamo]
        if (charPath) {
          strokeCount = charPath.strokes
          //    
          for (let i = 1; i <= strokeCount; i++) {
            const stroke = strokes[`stroke${i}`]
            if (stroke) {
              totalDuration += this.getStrokeDuration(stroke)
            }
          }
        }

        //    
        if (strokeCount > 1) {
          totalDuration += (strokeCount - 1) * 100
        }
      }

      return {
        start: 0,
        duration: totalDuration
      }
    })

    //   
    let currentTime = 0
    jamoTimings.forEach((timing) => {
      timing.start = currentTime
      currentTime += timing.duration + 150 // 300 150 
    })

    const animate = () => {
      const elapsed = Date.now() - this.startTime
      let index = jamoTimings.findLastIndex((timing) => timing.start <= elapsed)
      if (index === -1) index = 0

      const timing = jamoTimings[index]
      const progress = Math.min((elapsed - timing.start) / timing.duration, 1)

      const jamoPosition = index % 3
      let animationColor
      switch (jamoPosition) {
        case 0:
          animationColor = '#FFD700CC'
          break
        case 1:
          animationColor = '#9ACD32CC'
          break
        case 2:
          animationColor = '#FFD700CC' //'#B19CD9CC'
          break
        default:
          animationColor = CONFIG.paths.drawing.color
      }
      this.drawingService.setAnimationStyle(animationColor)

      //     TTS 
      if (index !== this.lastAnimatedIndex) {
        this.handleTTS(index)
        this.lastAnimatedIndex = index
      }

      this.animate(progress, handler.jamos[index], this.pathes[index])
      handler.drawStartPoints()

      //   
      if (progress >= 1 && index === jamos.length - 1) {
        this.isAnimating = false
        this.animationFrameId = null
        setTimeout(() => {
          // this.drawingService.clearCanvas()
          handler.drawPathes()
          handler.drawStartPoints()
        }, 1000)
      } else {
        requestAnimationFrame(animate)
      }
    }

    // this.drawingService.clearCanvas()
    animate()
  }

  get duration() {
    const { handler } = this
    const { jamos } = handler

    //      
    let totalDuration = 0
    jamos.forEach((jamo, index) => {
      const strokes = this.pathes[index]
      let jamoDuration = 0
      let strokeCount = 0

      for (let i = 1; i <= Object.keys(strokes).length; i++) {
        const stroke = strokes[`stroke${i}`]
        if (stroke) {
          jamoDuration += this.getStrokeDuration(stroke)
          strokeCount++
        }
      }

      jamoDuration += (strokeCount - 1) * 100 // 200 100 
      totalDuration += jamoDuration + 150 // 300 150 
    })

    return totalDuration
  }
}
