<script lang="ts">
  export let size: number = 500
  export let min: number = 200
  export let max: number = 600
  export let step: number = 100

  const decreaseSize = () => {
    console.log(' ')
    if (size - step >= min - 100) {
      size -= step
    }
  }

  const increaseSize = () => {
    console.log(' ')
    if (size + step <= max) {
      size += step
    }
  }
</script>

<div class="size-control">
  <label for=""></label>
  <button
    id="decreaseSize"
    class="size-button"
    on:click={decreaseSize}
    disabled={size <= min}
  >
    -
  </button>
  <span id="sizeValue">{size / 100 - 1}</span>
  <button
    id="increaseSize"
    class="size-button"
    on:click={increaseSize}
    disabled={size >= max}
  >
    +
  </button>
</div>

