import { SvelteStore } from 'svelte-store-class'
import { writable, type Writable } from 'svelte/store'
import { decomposeWord } from './hangul-utils'
import { WordPattern } from '../model/patterns'

export class WordStore extends SvelteStore<WordStore> {
  protected store: Writable<WordStore> = writable(this as WordStore)
  private index: number = 0
  constructor(readonly words: string[] = []) {
    super()
    if (words.length === 0) {
      words.push('')
    }
  }
  get currentWord() {
    return this.words[this.index]
  }
  reset(words: string[], index: number = 0) {
    this.words.splice(0, this.words.length)
    this.words.push(...words)
    this.index = index
    this.update()
  }
  selectAt(index: number) {
    let idx = Math.max(index, 0)
    idx = Math.min(idx, this.words.length - 1)
    if (this.index !== idx) {
      this.index = idx
      this.update()
    }
  }
  movePrev(): boolean {
    const idx = Math.max(0, this.index - 1)
    const changed = idx < this.index
    if (changed) {
      this.index = idx
      this.update()
    }
    return changed
  }
  moveNext(): boolean {
    const idx = Math.min(this.words.length - 1, this.index + 1)
    const changed = idx > this.index
    if (changed) {
      this.index = idx
      this.update()
    }
    return changed
  }
  getCurrentPattern() {
    return new WordPattern(decomposeWord(this.currentWord))
  }
}
