<svelte:options customElement={{ tag: 'ko-writing-node', shadow: 'none' }} />

<script lang="ts">
  import '@/assets/common.scss'
  import { type CanvasHandler } from './model/canvas-handler'
  import CanvasPanel from './CanvasPanel.svelte'
  import { writable, type Writable } from 'svelte/store'
  import { WordStore } from './lib/word-store'
  import { type KoWritingResponse } from './types/types'
  import ToleranceControl from './ToleranceControl.svelte'
  import FeedbackPanel from './FeedbackPanel.svelte'
  import { CharacterAnimation } from './view/char-animation'
  import { createEventDispatcher } from 'svelte'
  import SizeControl from './SizeControl.svelte'
  export let words: string[] = ['']
  export let index: number = 0
  export let canvasSize: number = 300
  export let tolerance: number = 40

  // console.log('[KOW]', words, index)
  const wordStore = new WordStore()
  let canvasHandler: CanvasHandler
  let activeChars: Writable<Jamo[]> = writable([])
  let result: KoWritingResponse | undefined = undefined

  const dispatch = createEventDispatcher<{ dir: -1 | 1 }>()

  const watchFeedback = (res: KoWritingResponse) => {
    result = res
    if (!res) {
      nextSyllable()
    }
  }
  const clearFeedback = () => {
    result = undefined
  }
  const changeWord = (dir: 1 | -1) => {
    canvasHandler.initialize()
    const pattern = wordStore.getCurrentPattern()
    canvasHandler.setPattern(pattern, pattern.chars)
    clearFeedback()
    dispatch('dir', dir)
  }
  const nextSyllable = () => {
    if (wordStore.moveNext()) {
      changeWord(1)
    }
  }
  const prevSyllable = () => {
    if (wordStore.movePrev()) {
      changeWord(-1)
    }
  }

  const startAnimation = () => {
    const animation = new CharacterAnimation(
      canvasHandler,
      canvasHandler.pathes
    )
    animation.start()
  }
  const replaceWords = (words: string[]) => {
    wordStore.reset(words, index)
    const pattern = wordStore.getCurrentPattern()
    canvasHandler.setPattern(pattern, pattern.chars)
  }
  const watchIndex = (idx) => {
    wordStore.selectAt(idx)
  }
  const watchCanvasSize = (size) => {
    if (canvasHandler) {
      canvasHandler.updateCanvasSize(size)
      replaceWords(words)
    }
  }
  $: {
    if (canvasHandler) {
      canvasHandler.updateTolerance(tolerance)
      canvasHandler.updateCanvasSize(300)
      replaceWords(words)
    }
  }
  $: watchIndex(index)
  $: watchCanvasSize(canvasSize)
</script>

<div class="control-section">
  <SizeControl bind:size={canvasSize} />
  <div class="button-container">
    <button class="prev-button" on:click={prevSyllable}></button>
    <button class="guide-button" on:click={startAnimation}></button>
    <button
      class="clear-button"
      on:click={() => {
        canvasHandler?.initialize()
        clearFeedback()
      }}></button
    >
    <button class="next-button" on:click={nextSyllable}></button>
  </div>
  <ToleranceControl bind:tol={tolerance} />
</div>
<div class="main-content">
  <div class="canvas-section">
    <div class="main-canvas">
      <FeedbackPanel {result} />
      <CanvasPanel
        ready={(handler) => (canvasHandler = handler)}
        feedback={watchFeedback}
        chars={$activeChars}
      />
    </div>
  </div>
</div>

