/**
 *    
 * @param char   
 * @returns , ,  
 */
export function decomposeHangul(char: string): Jamo[] {
  const charCode = char.charCodeAt(0)

  //    
  if (charCode < 0xac00 || charCode > 0xd7a3) {
    throw new Error('  .')
  }

  //   
  const baseCode = charCode - 0xac00
  const cho = Math.floor(baseCode / (21 * 28))
  const jung = Math.floor((baseCode % (21 * 28)) / 28)
  const jong = baseCode % 28

  return [CHOSUNG[cho] as Jamo, JUNGSUNG[jung] as Jamo, JONGSUNG[jong] as Jamo]
}

/**
 *      
 * @param word  
 * @returns    
 */
export function decomposeWord(word: string): Jamo[][] {
  return word.split('').map((char) => decomposeHangul(char))
}

/**
 *      
 * @param jamos    (2-3)
 * @returns     null
 */
export function combineJamo(jamos: Jamo[]): string | null {
  if (jamos.length < 2 || jamos.length > 3) {
    return null
  }

  const cho = CHOSUNG.indexOf(jamos[0] as (typeof CHOSUNG)[number])
  const jung = JUNGSUNG.indexOf(jamos[1] as (typeof JUNGSUNG)[number])
  const jong = jamos[2]
    ? JONGSUNG.indexOf(jamos[2] as (typeof JONGSUNG)[number])
    : 0

  if (cho !== -1 && jung !== -1 && jong !== -1) {
    return String.fromCharCode(0xac00 + cho * 21 * 28 + jung * 28 + jong)
  }
  return null
}
