// canvas-handler.ts
/// <reference lib="dom" />

import type { KoWritingResponse } from '../types/types'

import { StrokeEvaluator } from './stroke-evaluator'
import { DrawingService } from './drawing-service'
import { AudioService } from '../lib/audio'
import type { Jamo, Jaeum, Moeum } from './jamo' // Jaeum, Moeum 
import { NullRect, Rect } from './rect'
import { type WordPattern, Patterns } from './patterns'
import { CharState } from './char-state'
import { CHAR_PATHS, CONFIG, START_POINT_OFFSETS } from './canvas-config'
type KoWritingListener = (response: KoWritingResponse) => void

export class CanvasHandler {
  private listeners: KoWritingListener[] = []
  private drawingService: DrawingService

  private readonly audioService: AudioService
  private evaluator: StrokeEvaluator
  private charState = CharState.getInstance()
  /**
   * FIXME private   public .
   */
  public currentStroke: Array<[number, number]> = []
  public strokeHistory: Array<Array<[number, number]>> = []
  private isCompleted: boolean = true
  private isDrawingBroken: boolean = false
  private currentStrokeNumber = 1
  private _error: boolean = false
  private isDrawing: boolean = false
  private startTime: number = 0

  public jamos: Jamo[] = []
  private rects: Rect[] = []
  private _jamoIndex: number = 0
  private _pathes: any[] = []
  private _strokes: number[] = []
  public mode: 'jamo' | 'char' | 'word' = 'jamo'
  private currentEvalResult: KoWritingResponse | null = null

  constructor(readonly canvas: HTMLCanvasElement) {
    this.drawingService = new DrawingService(canvas)
    this.audioService = new AudioService()
    this.evaluator = new StrokeEvaluator(
      CONFIG.tolerance,
      undefined,
      undefined,
      1
    )
  }

  setPhoneme(jamos: Jamo[]) {
    if (JSON.stringify(this.jamos) === JSON.stringify(jamos)) {
      // console.log('   setPhoneme   ')
      return //    
    }
    this.jamos = jamos
    const pattern = Patterns.resolvePattern(jamos)
    if (!pattern) return
    this.rects = pattern.getRect(CONFIG.canvas.width, CONFIG.canvas.height)

    this.evaluator = new StrokeEvaluator(
      CONFIG.tolerance,
      undefined,
      undefined,
      1
    )

    // const nextChars = jamos.map((_, i) => jamos[i + 1])

    this._pathes = this.rects.map(({ x, y, w, h }, index) => {
      const ch = this.jamos[index]
      const nextChar = this.jamos[index + 1]
      this.charState.setNextChar(nextChar)
      return CHAR_PATHS[ch].calculateCoords(w, h, x, y)
    })

    this._strokes = this.jamos.map((jamo) => CHAR_PATHS[jamo].strokes)
    for (let k = 1; k < this._strokes.length; k++) {
      this._strokes[k] += this._strokes[k - 1]
    }
    this.initialize()
  }

  //   private setNextChar(char: Jamo | undefined) {
  //     nextCharValue = char
  //   }

  // Public interface methods
  initialize() {
    const { canvas } = this
    canvas.width = CONFIG.canvas.width
    canvas.height = CONFIG.canvas.height
    this.resetState()
    this.drawingService.clearCanvas()
    this.drawPathes()
    this.drawStartPoints()
    this.startTime = Date.now()
    this._jamoIndex = 0
  }

  setMode(tabId: string) {
    this.mode = tabId.startsWith('jamo')
      ? 'jamo'
      : tabId.startsWith('char')
      ? 'char'
      : 'word'
  }

  updateCanvasSize(size: number) {
    const prevWidth = CONFIG.canvas.width
    const prevHeight = CONFIG.canvas.height

    // Calculate size ratio
    const sizeRatio = size / 600
    this.updateConfigSizes(sizeRatio)

    // Update canvas dimensions based on mode
    if (this.mode === 'word' && this.rects.length > 0) {
      const charCount = Math.ceil(this.jamos.length / 3)
      CONFIG.canvas.width = size * charCount
      CONFIG.canvas.height = size
      this.drawingService.updateCanvasSize(size * charCount, size)
    } else {
      CONFIG.canvas.width = size
      CONFIG.canvas.height = size
      this.drawingService.updateCanvasSize(size, size)
    }

    // Skip if no rects to update
    if (this.rects.length === 0) {
      this.reinitializeWithState()
      return
    }

    // Calculate scaling ratios
    const widthRatio = CONFIG.canvas.width / prevWidth
    const heightRatio = CONFIG.canvas.height / prevHeight

    // Update rects with proper scaling
    this.rects = this.rects.map((rect) => {
      if (rect instanceof NullRect) return rect

      if (this.mode === 'word') {
        // For word mode, maintain height while scaling width
        return new Rect(
          rect.x * widthRatio,
          rect.y,
          rect.w * widthRatio,
          rect.h
        )
      } else {
        // For jamo and char modes, scale both dimensions
        return new Rect(
          rect.x * widthRatio,
          rect.y * heightRatio,
          rect.w * widthRatio,
          rect.h * heightRatio
        )
      }
    })

    // Recalculate paths with new dimensions
    this._pathes = this.rects.map((rect, i) => {
      const ch = this.jamos[i]
      if (ch === '_') return null

      const nextChar = this.jamos[i + 1]
      this.charState.setNextChar(nextChar)
      return rect instanceof NullRect
        ? null
        : this.calculateCharCoords(ch, rect)
    })

    // Reinitialize while maintaining current state
    this.reinitializeWithState()
  }

  private reinitializeWithState() {
    const { canvas } = this
    canvas.width = CONFIG.canvas.width
    canvas.height = CONFIG.canvas.height

    //  
    this.drawingService.clearCanvas()
    this.drawPathes()
    this.drawStartPoints()

    //     
    //this.redrawHistory()
    if (this.strokeHistory.length > 0) {
      this.drawingService.drawHistory()
    }
  }

  updateTolerance(tol: number) {
    CONFIG.tolerance = tol
    this.evaluator = new StrokeEvaluator(tol)
    this.initialize()
  }

  setPattern(pattern: WordPattern, chars: Jamo[][]) {
    const prevWidth = CONFIG.canvas.width
    const baseHeight = CONFIG.canvas.height
    this.mode = 'word'
    this.jamos = chars.flatMap((ch) => ch)

    //       
    const charCount = chars.length
    const width = baseHeight * charCount //     
    CONFIG.canvas.width = width
    this.rects = pattern.getRect(width, CONFIG.canvas.height)

    //      
    // if (prevWidth !== width) {
    //   const widthRatio = width / prevWidth
    //   this.rects = this.rects.map((rect) =>
    //     rect instanceof NullRect
    //       ? rect
    //       : new Rect(rect.x * widthRatio, rect.y, rect.w * widthRatio, rect.h)
    //   )
    // }

    this.evaluator = new StrokeEvaluator(
      CONFIG.tolerance,
      undefined,
      undefined,
      chars.length //    
    )

    this.setupPathesAndStrokes()
    // this.initialize()
    this.reinitializeWithState()
  }

  // Event binding
  bindEventListener(listener: KoWritingListener) {
    const options: AddEventListenerOptions = { passive: false }

    this.canvas.addEventListener(
      'touchstart',
      (e) => this.handleStart(e),
      options
    )
    this.canvas.addEventListener(
      'touchmove',
      (e) => this.handleMove(e),
      options
    )
    this.canvas.addEventListener('touchend', (e) => this.handleEnd(e), options)
    this.canvas.addEventListener('mousedown', (e) => this.handleStart(e))
    this.canvas.addEventListener('mousemove', (e) => this.handleMove(e))
    this.canvas.addEventListener('mouseup', (e) => this.handleEnd(e))
    this.canvas.addEventListener('mouseleave', (e) => this.handleEnd(e))

    document.addEventListener(
      'touchstart',
      () => {
        if (window.speechSynthesis) {
          window.speechSynthesis.speak(new SpeechSynthesisUtterance(''))
        }
      },
      { once: true }
    )

    this.updateCanvasSize(500)
    if (listener) {
      this.listeners.push(listener)
    }
  }

  // Event handlers
  handleStart(e: TouchEvent | MouseEvent) {
    e.preventDefault()

    const coords = this.getCoordinates(e)
    console.log('HandleStart coords:', coords)
    console.log('Current jamo index:', this._jamoIndex)
    console.log('Current jamos:', this.jamos)
    console.log('Current paths:', this._pathes)
    console.log('Current CONFIG.tolerance:', CONFIG.tolerance)

    if (this.isNearStrokeStart(coords.x, coords.y)) {
      if (this._error) {
        this.drawingService.clearCanvas()
        this.drawPathes()
        this.drawStartPoints()
        this.redrawHistory()
      }

      this.drawingService.setAnimationStyle(CONFIG.paths.drawing.color)

      this.isDrawing = true
      this.isDrawingBroken = false
      this.currentStroke = [[coords.x, coords.y]]
      this.startTime = Date.now()

      this.notifyListeners({
        success: true,
        stroke: { current: 0, total: this.totalStrokes },
      })
    }
  }

  handleMove(e: TouchEvent | MouseEvent) {
    e.preventDefault()
    if (!this.isDrawing) return

    const coords = this.getCoordinates(e)
    this.currentStroke.push([coords.x, coords.y])
    this.drawingService.drawUserStroke(this.currentStroke)
  }

  handleEnd(e: TouchEvent | MouseEvent) {
    e.preventDefault()
    if (!this.isDrawing) return

    const evalResult = this.evaluateStroke()
    this.currentEvalResult = evalResult
    console.log('Evaluation result:', evalResult)

    if (evalResult.success) {
      this.handleSuccessfulStroke()
    } else {
      this._error = true
      this.showFailFeedback()
    }

    this.isDrawing = false
    this.currentStroke = []
  }

  // Helper methods
  private updateConfigSizes(sizeRatio: number) {
    CONFIG.paths.guide.width = Math.max(2, Math.floor(4 * sizeRatio))
    CONFIG.paths.drawing.width = Math.max(8, Math.floor(32 * sizeRatio))
    CONFIG.paths.background.widthOffset = Math.max(
      15,
      Math.floor(60 * sizeRatio)
    )
    CONFIG.startPoint.radius = Math.max(4, Math.floor(15 * sizeRatio))
    CONFIG.animation.arrowSize = Math.max(3, Math.floor(12 * sizeRatio))

    //  CONFIG.tolerance = Math.max(5, Math.floor(10 * sizeRatio))
    CONFIG.minStrokeLength = Math.max(25, Math.floor(50 * sizeRatio))
  }

  private setupPathesAndStrokes() {
    this._pathes = this.rects.map((rect, i) => {
      const ch = this.jamos[i]
      if (ch === '_') return null

      const nextChar = this.jamos[i + 1]
      this.charState.setNextChar(nextChar)
      return rect instanceof NullRect
        ? null
        : this.calculateCharCoords(ch, rect)
      // CHAR_PATHS[ch].calculateCoords(rect.w, rect.h, rect.x, rect.y)
    })

    this._strokes = this.jamos.map((jamo) =>
      jamo === '_' ? 0 : this.getStrokeCount(jamo)
    )
    // this._strokes = this.jamos.map((jamo) =>
    //     jamo === '_' ? 0 : CHAR_PATHS[jamo].strokes
    // )

    for (let k = 1; k < this._strokes.length; k++) {
      this._strokes[k] += this._strokes[k - 1]
    }
  }

  // private redrawHistory() {
  //   this.strokeHistory.forEach((stroke) =>
  //     this.drawingService.drawUserStroke(stroke)
  //   )
  // }

  private redrawHistory() {
    this.drawingService.setAnimationStyle(CONFIG.paths.drawing.color)
    this.strokeHistory.forEach((stroke) => {
      this.drawingService.drawUserStroke(stroke)
    })
    this.drawingService.restore()
  }

  private notifyListeners(response: KoWritingResponse) {
    // console.log('Notifying listeners with response:', response)
    this.listeners.forEach((listener) => listener(response))
  }

  // Drawing methods
  drawPathes() {
    if (this.jamos.length === 0) return

    this._pathes.forEach((path, index) => {
      // const offset = index === 0 ? 0 : this._strokes[index - 1]
      this.drawPath(path, this.jamos[index])

      // const rect = this.rects[index]
      // this.drawingService.drawRect(rect)
    })
  }

  drawStartPoints() {
    this._pathes.forEach((path, index) => {
      if (!path) return
      const offset = index === 0 ? 0 : this._strokes[index - 1]
      const jamo = this.jamos[index]
      if (jamo === '_' || !jamo) return

      if (!(jamo in START_POINT_OFFSETS)) return
      const offsets =
        START_POINT_OFFSETS[jamo as keyof typeof START_POINT_OFFSETS]
      if (!offsets) return

      const radius = CONFIG.startPoint.radius
      const offsetDist = radius * 2

      offsets.forEach((offsetData, i) => {
        const strokeNum = i + 1
        const stroke = path[`stroke${strokeNum}`]
        if (!stroke?.start) return

        const x = stroke.start.x + offsetData.x * offsetDist
        const y = stroke.start.y + offsetData.y * offsetDist
        this.drawingService.drawStartPoint(x, y, strokeNum + (offset || 0))
      })
    })
  }

  private drawPath(coords: any, character: Jamo | undefined) {
    if (!coords || character === '_') return

    //       
    const nextChar = this.jamos[this._jamoIndex + 1]
    this.charState.setNextChar(nextChar)

    // coords   
    // console.log('Drawing path for:', {
    //   character,
    //   coords,
    //   strokeOffset,
    //   nextChar
    // })

    this.drawingService.drawStrokePath(coords)
    this.drawEndPoints(coords, character)
  }

  private drawEndPoints(coords: any, character: Jamo | undefined) {
    if (!character) return
    let strokeCount = this.getStrokeCount(character)
    for (let i = 1; i <= strokeCount; i++) {
      const stroke = coords[`stroke${i}`]
      if (stroke) {
        if (stroke.type === 'circle') {
          this.drawingService.drawEndPoint(stroke.start.x, stroke.start.y)
        } else {
          this.drawingService.drawEndPoint(
            stroke.end ? stroke.end.x : stroke.middle.x,
            stroke.end ? stroke.end.y : stroke.middle.y
          )
        }
      }
    }
  }

  // State management methods
  private resetState() {
    this.isDrawing = false
    this.currentStroke = []
    this.strokeHistory = []
    this.isCompleted = false
    this.isDrawingBroken = false
    this.currentStrokeNumber = 1
    this._error = false
  }

  private handleSuccessfulStroke() {
    this.strokeHistory.push([...this.currentStroke])
    this.drawingService.addStrokeToHistory(this.currentStroke)

    if (this.currentStrokeNumber < this.totalStrokes) {
      this.showRunningFeedback()
    } else {
      this.isCompleted =
        this.mode === 'jamo' ? this._jamoIndex + 1 === this.jamos.length : true

      if (this.isCompleted) {
        this.showSuccessFeedback()
      }
      this.currentStrokeNumber = 0
      // if (this.mode === 'jamo') {
      //   this.isCompleted = this._jamoIndex + 1 === this.jamos.length
      // } else {
      //   this.isCompleted = true
      // }

      // if (this.isCompleted) {
      //   this.showSuccessFeedback()
      // }
      // this.currentStrokeNumber = 0
    }

    if (this._strokes[this._jamoIndex] === this.currentStrokeNumber) {
      this._jamoIndex++
    }
    this.currentStrokeNumber++
  }

  private evaluateStroke(): KoWritingResponse {
    const { currentStroke, isDrawingBroken } = this
    if (currentStroke.length < 2) {
      return {
        success: false,
        stroke: {
          current: this.currentStrokeNumber,
          total: this.totalStrokes,
        },
      }
    }

    if (!this.currentChar) throw new Error('No current character')

    // const currentCharIndex = Math.floor(this._jamoIndex / 3)
    const totalChars = Math.ceil(this.jamos.length / 3)

    this.evaluator = new StrokeEvaluator(
      CONFIG.tolerance,
      this.currentChar as Jaeum | Moeum,
      this.revisedStrokeNum,
      totalChars
    )

    const coords = this._pathes[this._jamoIndex]
    const currentStrokeCoords = coords?.[`stroke${this.revisedStrokeNum}`]

    console.log('Evaluate Stroke Debug:', {
      jamoIndex: this._jamoIndex,
      revisedStrokeNum: this.revisedStrokeNum,
      coords: coords,
      currentStrokeCoords: currentStrokeCoords,
    })

    if (!currentStrokeCoords) {
      return {
        success: false,
        stroke: {
          current: this.currentStrokeNumber,
          total: this.totalStrokes,
        },
      }
    }

    this.evaluator.setCurrentChar(this.currentChar as Jaeum | Moeum)
    this.evaluator.setStrokeNum(this.revisedStrokeNum)
    //      
    // this.evaluator.setJamoIndex(this._jamoIndex)

    let evaluationResult
    try {
      evaluationResult = this.evaluator[
        currentStrokeCoords.type === 'circle'
          ? 'evaluateCircle'
          : currentStrokeCoords.type === 'quadratic'
          ? 'evaluateQuadratic'
          : currentStrokeCoords.type === 'path'
          ? 'evaluatePath'
          : 'evaluateLine'
      ](currentStroke, currentStrokeCoords, isDrawingBroken)

      // const isSuccessful = Object.values(evaluationResult).every(
      //   (value) => value === true
      // )

      const response = {
        success: Object.values(evaluationResult).every(
          (value) => value === true
        ),
        stroke: {
          current: this.currentStrokeNumber,
          total: this.totalStrokes,
          type: currentStrokeCoords.type, //   
        },
        detail: evaluationResult,
      }

      //    
      // console.log('Evaluation result:', response)

      return response
    } catch (error) {
      console.error('Stroke evaluation error:', {
        error,
        currentChar: this.currentChar,
        strokeNum: this.revisedStrokeNum,
        coords: currentStrokeCoords,
      })

      return {
        success: false,
        stroke: {
          current: this.currentStrokeNumber,
          total: this.totalStrokes,
        },
      }
    }
  }

  private isNearStrokeStart(x: number, y: number): boolean {
    console.log('isNearStrokeStart called:', { x, y })
    console.log('Current char:', this.currentChar)
    console.log('Current stroke number:', this.revisedStrokeNum)
    console.log('Current tolerance:', CONFIG.tolerance)

    if (!this.currentChar) return false

    const tolerance = CONFIG.tolerance
    const radius = CONFIG.startPoint.radius
    const offset = radius * 2

    let currentStrokeNumber = this.revisedStrokeNum
    let currentJamo = this.jamos[this._jamoIndex]
    let coords = this._pathes[this._jamoIndex]

    while (currentJamo === '_') {
      this._jamoIndex++
      if (this._jamoIndex >= this.jamos.length) {
        return false
      }
      currentJamo = this.jamos[this._jamoIndex]
      coords = this._pathes[this._jamoIndex]
    }

    console.log('After skipping empty chars:', {
      newIndex: this._jamoIndex,
      newChar: currentJamo,
      coords: coords,
    })

    const currentStrokeCoords = coords?.[`stroke${currentStrokeNumber}`]
    if (!currentStrokeCoords) return false

    // let startPoint: { x: number; y: number }
    if (!this.currentChar || !(currentJamo in START_POINT_OFFSETS)) return false
    const offsets =
      START_POINT_OFFSETS[currentJamo as keyof typeof START_POINT_OFFSETS]
    const currentOffset = offsets?.[currentStrokeNumber - 1]

    if (!currentOffset) return false

    const startPoint = currentStrokeCoords.start
    const offsetX = currentOffset.x * offset
    const offsetY = currentOffset.y * offset

    return (
      Math.hypot(x - (startPoint.x + offsetX), y - (startPoint.y + offsetY)) <
      tolerance
    )
  }

  // Feedback methods
  private showSuccessFeedback() {
    const response: KoWritingResponse = {
      success: true,
      stroke: {
        current: this.currentStrokeNumber,
        total: this.totalStrokes,
      },
    }
    this.notifyListeners(response)
    this.audioService.playSound()
  }

  private showFailFeedback() {
    const response: KoWritingResponse = {
      success: false,
      stroke: {
        current: this.currentStrokeNumber,
        total: this.totalStrokes,
        type: this._pathes[this._jamoIndex][`stroke${this.revisedStrokeNum}`]
          ?.type,
      },
      detail: this.currentEvalResult?.detail,
    }
    this.notifyListeners(response)
  }

  private showRunningFeedback() {
    const response: KoWritingResponse = {
      success: true,
      stroke: {
        current: this.currentStrokeNumber,
        total: this.totalStrokes,
        type: this._pathes[this._jamoIndex][`stroke${this.revisedStrokeNum}`]
          ?.type,
      },
    }
    this.notifyListeners(response)
  }

  private getCoordinates(e: TouchEvent | MouseEvent) {
    const rect = this.canvas.getBoundingClientRect()
    const coords =
      e instanceof TouchEvent
        ? { x: e.touches[0].clientX, y: e.touches[0].clientY }
        : { x: e.clientX, y: e.clientY }

    return {
      x: coords.x - rect.left,
      y: coords.y - rect.top,
    }
  }

  private calculateCharCoords(char: Jamo, rect: Rect) {
    if (char === '_') return null
    return CHAR_PATHS[char].calculateCoords(rect.w, rect.h, rect.x, rect.y)
  }

  private getStrokeCount(jamo: Jamo): number {
    return CHAR_PATHS[jamo].strokes
  }

  // Getters and utility methods
  get currentChar(): Jamo | undefined {
    return this.jamos[this._jamoIndex]
  }

  get totalStrokes(): number {
    return this.jamos
      .map((jamo) => this.getStrokeCount(jamo))
      .reduce((sum, stroke) => sum + stroke, 0)
  }

  get revisedStrokeNum() {
    // let stroke = this.currentStrokeNumber
    // if (this._jamoIndex === 2) {
    //   return stroke - this._strokes[1]
    // } else if (this._jamoIndex === 1) {
    //   return stroke - this._strokes[0]
    // }
    // return stroke

    // let stroke = this.currentStrokeNumber
    // //    
    // const charIndex = Math.floor(this._jamoIndex / 3)
    // if (charIndex > 0) {
    //   //    
    //   const previousStrokes = this._strokes[charIndex * 3 - 1]
    //   return stroke - previousStrokes
    // }
    // return stroke
    let stroke = this.currentStrokeNumber

    //     (3   )
    const currentCharIndex = Math.floor(this._jamoIndex / 3)

    if (currentCharIndex > 0) {
      //     
      const prevCharLastJamoIndex = currentCharIndex * 3 - 1

      if (this._jamoIndex % 3 === 1) {
        //    
        return stroke - this._strokes[currentCharIndex * 3]
      } else if (this._jamoIndex % 3 === 2) {
        //    
        return stroke - this._strokes[currentCharIndex * 3 + 1]
      }

      //     
      return stroke - this._strokes[prevCharLastJamoIndex]
    } else if (this._jamoIndex === 1) {
      //     ()  
      return stroke - this._strokes[0]
    } else if (this._jamoIndex === 2) {
      //     ()  
      return stroke - this._strokes[1]
    }

    return stroke
  }

  get configuration() {
    return CONFIG
  }

  get currentJamoIndex(): number {
    return this._jamoIndex
  }

  get strokesArray(): number[] {
    return this._strokes
  }

  get characterPath() {
    return this.currentChar ? CHAR_PATHS[this.currentChar] : undefined
  }

  get pathes(): any[] {
    return this._pathes
  }
}
