// char-state.ts
import type { Jamo } from './jamo'

export class CharState {
  private static instance: CharState
  private nextChar: Jamo | undefined = undefined

  private constructor() {}

  static getInstance(): CharState {
    if (!CharState.instance) {
      CharState.instance = new CharState()
    }
    return CharState.instance
  }

  setNextChar(char: Jamo | undefined) {
    this.nextChar = char
  }

  getNextChar(): Jamo | undefined {
    return this.nextChar
  }
}
