import type { Jamo } from './jamo'
import { Rect } from './rect'
import { RegionBlock } from './region-block'
import * as PatternClasses from './patterns'

//   
export abstract class BasePattern {
  protected regions: RegionBlock[]

  constructor(regions: RegionBlock[]) {
    this.regions = regions
  }

  getRect(width: number, height: number) {
    return this.regions.map(
      (region) =>
        new Rect(
          region.offsetX * width,
          region.offsetY * height,
          region.width * width,
          region.height * height
        )
    )
  }
}

