/**
 *     
 *     
 */
export class WordPattern {
  patterns: BasePattern[]
  constructor(readonly chars: Jamo[][]) {
    // const regions = Array(syllableCount).fill(0).map((_, i) => {
    //   const x = i * (syllableWidth + gap);
    //   return new RegionBlock(x, 0, syllableWidth, 1);
    // });
    this.patterns = chars.flatMap((jamos) => {
      const pattern = Patterns.resolvePattern(jamos)
      return pattern
    })
  }
  getRect(width: number, height: number): Rect[] {
    //   
    const widthPerChar = width / this.patterns.length
    return this.patterns.flatMap((pattern, index) => {
      const rects = pattern.getRect(widthPerChar, height)
      rects.forEach((rect) => {
        rect.setX(rect.x + index * widthPerChar)
      })
      //  '_'  3 rect  
      while (rects.length < 3) {
        // NullRect      Rect 
        const emptyRect = new Rect(
          rects[0].x, //  x  
          rects[0].y + rects[0].h, //  rect  
          rects[0].w, //   
          0 //  0 
        )
        rects.push(emptyRect)
      }
      return rects
    })
  }
}
