<script lang="ts">
  export let tol: number = 10
  export let min: number = 10
  export let max: number = 40
  export let step: number = 5

  const decreaseTolerance = () => {
    if (tol - step >= min) {
      tol -= step
    }
  }

  const increaseTolerance = () => {
    if (tol + step <= max) {
      tol += step
    }
  }
</script>

<div class="tol-control">
  <label for=""></label>
  <button
    id="decreaseTol"
    class="tol-button"
    on:click={decreaseTolerance}
    disabled={tol <= min}
  >
    -
  </button>
  <span id="tolValue">{tol / 5 - 1}</span>
  <button
    id="increaseTol"
    class="tol-button"
    on:click={increaseTolerance}
    disabled={tol >= max}
  >
    +
  </button>
</div>

